import React from "react";
import {Link} from "react-router-dom";

function Story() {
  return (
    <>
      <section id="brandStory">
        <div className="inner">
          <div className="button_whale">
            <img src={require('../images/scrollWhale.png')} alt="경로문제" />
          </div>
          <figcaption>
            <h4>Brand Story</h4>
            <h2>사용자가 1순위가 되는 브라우저</h2>
          </figcaption>
          <figure>
            <img
              src={require("../images/sub_brandStroy/notebook.png")}
              alt="경로잘못됨"
            />
            <div className="video">
              <video
                src={require("../images/sub_brandStroy/cloud.mp4")}
                autoPlay
                muted
                loop
              ></video>
            </div>
          </figure>
        </div>
        <p>이것이 바로 웨일이 추구하는 목표이자, 서비스의 가치입니다.</p>
      </section>

      <section id="sns_wrap">
        <h3>
          <img
            src={require("../images/sub_brandStroy/brand_icon.png")}
            alt="경로 잘못 됨"
          />
          <p>
            네이버 웨일의 <span>다양한 소식과 즐거운 소통</span>
          </p>
          <p>웨일 공식 SNS 채널에서 만나보세요!</p>
        </h3>
        <ul>
          <li><Link to={"/blog"}><img src={require('../images/sub_brandStroy/sns_blog.png')} alt="경로잘못됨" /></Link></li>
          <li><Link to={"/tv"}><img src={require('../images/sub_brandStroy/sns_tv.png')} alt="경로잘못됨" /></Link></li>
          <li><Link to={"/insta"}><img src={require('../images/sub_brandStroy/sns_insta.png')} alt="경로잘못됨" /></Link></li>
          <li><Link to={"/facebook"}><img src={require('../images/sub_brandStroy/sns_facebook.png')} alt="경로잘못됨" /></Link></li>
          <li><Link to={"/youtube"}><img src={require('../images/sub_brandStroy/sns_youtube.png')} alt="경로잘못됨" /></Link></li>
          <li><Link to={"/twitter"}><img src={require('../images/sub_brandStroy/sns_twitter.png')} alt="경로잘못됨" /></Link></li>
        </ul>
      </section>
    </>
  );
}

export default Story;
