import React from "react";
import {Link} from "react-router-dom";
import { BsDownload } from 'react-icons/bs';

function Footer() {
  return (
    <footer id="footer">
      <div className="inner">
      <h2>NAVER whale</h2>
      <ul className="ft_gnb">
        <li><Link to={"/footer_tab1"}>이용약관</Link></li>
        <li><Link to={"/footer_tab2"}>개인정보 처리방침</Link></li>
        <li><Link to={"/footer_tab3"}>Naver Whale 개인정보 보호를 위한 백서</Link></li>
        <li><Link to={"/footer_tab4"}>제휴 제안</Link></li>
        <li><Link to={"/footer_tab5"}>고객센터</Link></li>
        <li><Link to={"/footer_tab6"}>&copy;윤정효</Link></li>
      </ul>
      <button className="beta_download">
        <Link to={"/download/win"}><BsDownload/> 베타 다운로드</Link>
      </button>
      </div>
    </footer>
  );
}

export default Footer;