import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import $ from 'jquery';

function Header() {
 useEffect(()=>{
  $('.dropdown_title li').on({
    'mouseover':function(){
      $('.dropdown_sub').css('display','block').stop().slideDown(800);
    }
  });
  $('.dropdown_sub').on({
    'mouseleave':function(){
      $('.dropdown_sub').css('display','block').stop().slideUp(800);
    }
  });
 });
  return (
    <header id="header">
      <div className="header_inner">
        <h2 className="logo_text">
          {/* Link to browser router : process.env.PUBLIC_URL */}
          <Link to={"/ko"}>
            NAVER <span>whale</span>
          </Link>
        </h2>
        <div className="inner">
          <ul className="dropdown_title">
            <li>
              <Link to={"/ko/story"}>Surfing My Way</Link>
            </li>
            <li>
              <Link to={"/tab1"}>가이드</Link>
            </li>
            <li>
              <Link to={"/tab2"}>스토어</Link>
            </li>
            <li>
              <Link to={"/tab3"}>연구소</Link>
            </li>
            <li>
              <Link to={"/tab4"}>웨일 스페이스</Link>
            </li>
            <li>
              <Link to={"/tab5"}>웨일 북</Link>
            </li>
          </ul>
        </div>
        <button className="loginForm">
          <Link to={"/Login"}>로그인</Link>
        </button>
        <button className="signForm">
          <Link to={"/SignUp"}>회원가입</Link>
        </button>
      </div>
      <div className="dropdown_sub">
        <div className="inner">
          <div className="sub_wrap">
            <ul>
              <li><Link to={"/ko/story/"}>Brand Story</Link></li>
              <li><Link to={"/ko/details/omnitasking"}>주요 기능</Link></li>
            </ul>
            <ul>
              <li><Link to={"/sub_tab"}>What's NEW</Link></li>
              <li><Link to={"/sub_tab"}>웨일 이용가이드</Link></li>
              <li><Link to={"/sub_tab"}>마이 액티비티</Link></li>
            </ul>
            <ul>
              <li><Link to={"/sub_tab"}>확장앱</Link></li>
              <li><Link to={"/sub_tab"}>배경테마</Link></li>
              <li><Link to={"/sub_tab"}>개발자센터</Link></li>
            </ul>
            <ul>
              <li><Link to={"/sub_tab"}>연구소 홈</Link></li>
              <li><Link to={"/sub_tab"}>베스트연구</Link></li>
              <li><Link to={"/sub_tab"}>버그바운티</Link></li>
            </ul>
            <ul>
              <li><Link to={"/sub_tab"}>제품 소개</Link></li>
              <li><Link to={"/sub_tab"}>기능</Link></li>
              <li><Link to={"/sub_tab"}>제휴 서비스</Link></li>
            </ul>
            <ul>
              <li><Link to={"/sub_tab"}>웨일북 소개</Link></li>
              <li><Link to={"/sub_tab"}>웨일 OS</Link></li>
              <li><Link to={"/sub_tab"}>디바이스 정보</Link></li>
            </ul>
          </div>
        </div>
      </div>
    </header>
  );
}

export default Header;
