// components/MainSkill/ 3개 파일 통합 이동경로
import React, { useState } from "react";

import Omnitasking from "./MainSkill/Omnitasking";
import Security from "./MainSkill/Security";
import Custom from "./MainSkill/Custom";

import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import {BsCheck} from "react-icons/bs"


export default function MainSkill() {
  const [key, setKey] = useState("옴니태스킹");

  return (
    <>
      <Tabs
        id="controlled-tab-example"
        activeKey={key}
        onSelect={(k) => setKey(k)}
        className="mb-3"
      >
        <Tab eventKey="옴니태스킹" title={<span><BsCheck/>옴니태스킹</span>}>
          <Omnitasking />
        </Tab>
        <Tab eventKey="강력한 보안" title={<span><BsCheck/>강력한 보안</span>}>
          <Security />
        </Tab>
        <Tab eventKey="커스터마이징" title={<span><BsCheck/>커스터마이징</span>}>
          <Custom />
        </Tab>
      </Tabs>
    </>
  );
}
